<template>
  <div class="page-box">
    <div class="wapper-box">
      <div class="wapper-left">
        <div class="left-header">
          <el-input
            placeholder="请输入内容"
            v-model="groupForm.keyword"
            class="input-search"
          >
            <template slot="append">
              <i class="el-icon-search" @click="searchSection"></i>
            </template>
          </el-input>
        </div>
        <div class="left-btn-box">
          <el-button type="primary" @click="addClassifyHandle"
            >新增分组</el-button
          >
          <el-button @click="deleteAllHandle">批量删除</el-button>
        </div>
        <div class="left-content">
          <el-checkbox-group
            v-model="groupSelects"
            v-if="groupLists.length > 0"
          >
            <div
              class="check-item"
              v-for="item in groupLists"
              :key="item.section_id"
              :class="{
                'check-item-active': item.section_id === groupForm.section_id,
              }"
              @click="selectGroup(item)"
            >
              <el-checkbox :label="item.section_id">
                <div class="check-label">{{ item.section_name }}</div>
              </el-checkbox>
              <i
                class="el-icon-plus icon-edit"
                title="添加群到分组"
                @click.stop="addSectionHandle(item)"
              ></i>
              <i
                class="el-icon-edit icon-edit"
                title="编辑分组"
                @click.stop="editHandle(item)"
              ></i>
              <i
                class="el-icon-delete icon-delete"
                title="删除分组"
                @click.stop="deleteHandle(item)"
              ></i>
            </div>
          </el-checkbox-group>
          <div class="emtry-box" v-if="groupLists.length === 0">暂无数据</div>
        </div>
      </div>
      <div class="wapper-right">
        <div class="right-header">
          <div>
            <el-button @click="deleteAllGroupHandle">批量移除</el-button>
          </div>
          <el-input
            placeholder="请输入内容"
            v-model="groupForm.word"
            class="input-right-search"
          >
            <template slot="append">
              <i class="el-icon-search" @click="searchGroup"></i>
            </template>
          </el-input>
        </div>
        <div class="right-content" ref="contentRef">
          <el-table
            :data="markList"
            border
            style="width: 100%"
            :height="tableHeight"
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="55" align="center">
            </el-table-column>
            <el-table-column prop="group_title" label="群名" align="center">
            </el-table-column>
            <el-table-column prop="owner_name" label="群所有者" align="center">
            </el-table-column>
            <el-table-column
              prop="inviter_name"
              label="群邀请人"
              align="center"
            >
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="text"
                  @click="deleteGroupSingleHandle(scope.row)"
                  >移除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <add-classify
      v-if="showClassify"
      @close="closeHandle"
      :type="operType"
      :data="groupObject"
      @save="saveClassifyHandle"
    ></add-classify>
    <section-add
      v-if="showSection"
      :id="id"
      @close="showSection = false"
      :selectList="selectSections"
      @save="saveSectionHandle"
    ></section-add>
  </div>
</template>
<script>
import {
  GetGroupSectionRequest,
  GetSectionRequest,
  CreateSectionRequest,
  UpdataSectionRequest,
  DeleteSectionRequest,
  AddGroupToSectionRequest,
  RemoveGroupFormSectionRequest,
} from "@/api/app";
import addClassify from "./components/addClassify.vue";
import sectionAdd from "./components/sectionAdd.vue";
export default {
  components: {
    addClassify,
    sectionAdd,
  },
  data() {
    return {
      id: "5415202864",
      groupLists: [],
      groupAllLists: [],
      groupForm: {
        keyword: "",
        word: "",
        section_id: "",
      },
      groupSelects: [],
      tableHeight: 200,
      markList: [],
      markAllLists: [],
      showClassify: false,
      operType: "add",
      groupObject: {},

      showSection: false,
      selectSections: [],
      selectSectionsCopy: [],
      addSectionId: "",
      selectGroupList: [],
    };
  },
  mounted() {
    this.id = this.$route.query.id || "5415202864";
    this.getGroupData();
    this.initHandle();
  },
  methods: {
    initHandle() {
      this.$nextTick(() => {
        this.tableHeight = this.$refs.contentRef.clientHeight;
      });
    },
    selectGroup(item) {
      this.groupForm.section_id = item.section_id;
      this.getSectionData();
    },
    getGroupData() {
      GetGroupSectionRequest({ id: this.id })
        .then((res) => {
          if (res.code === 200) {
            this.groupLists = res.data || [];
            this.groupAllLists = JSON.parse(JSON.stringify(res.data));
            this.groupForm.section_id =
              (this.groupLists[0] && this.groupLists[0].section_id) || "";
            this.getSectionData();
          }
        })
        .catch(() => {
          this.groupLists = [];
          this.groupAllLists = [];
          this.groupForm.section_id = "";
          this.markList = [];
          this.markAllLists = [];
        });
    },
    getDialogSectionData(section_id) {
      let params = {
        id: this.id,
        section_id,
      };
      GetSectionRequest(params).then((res) => {
        if (res.code === 200) {
          let list = res.data || [];
          this.selectSections = list.map((d) => d.group_id);
          this.selectSectionsCopy = JSON.parse(
            JSON.stringify(this.selectSections)
          );
          this.showSection = true;
        }
      });
    },
    getSectionData() {
      let params = {
        id: this.id,
        section_id: this.groupForm.section_id,
      };
      GetSectionRequest(params).then((res) => {
        if (res.code === 200) {
          this.markList = res.data || [];
          this.markAllLists = JSON.parse(JSON.stringify(res.data));
        }
      });
    },
    addSectionHandle(item) {
      this.addSectionId = item.section_id;
      this.getDialogSectionData(item.section_id);
    },
    closeHandle() {
      this.showClassify = false;
    },
    addClassifyHandle() {
      this.operType = "add";
      this.showClassify = true;
    },
    editHandle(item) {
      this.operType = "edit";
      this.groupObject = item;
      this.showClassify = true;
    },
    saveClassifyHandle(item) {
      let request = null;
      if (this.operType === "add") {
        request = CreateSectionRequest;
      } else if (this.operType === "edit") {
        request = UpdataSectionRequest;
      }
      request({ id: this.id }, item).then((res) => {
        if (res.code === 200) {
          this.closeHandle();
          this.getGroupData();
          this.$message({
            type: "success",
            message: "保存成功",
          });
        }
      });
    },
    saveSectionHandle(list) {
      console.log(list, this.selectSectionsCopy);
      let addList = [];
      let deleteList = [];
      this.selectSectionsCopy.forEach((d) => {
        let item = list.find((k) => k === d);
        if (!item) {
          deleteList.push(d);
        }
      });
      list.forEach((d) => {
        let item = this.selectSectionsCopy.find((k) => k === d);
        if (!item) {
          addList.push(d);
        }
      });
      console.log(90, addList, deleteList);
      let addPromise = new Promise((resolve, reject) => {
        if(addList.length === 0) {
          resolve();
          return
        }
        AddGroupToSectionRequest(
          { id: this.id },
          { group_ids: addList, section_id: this.addSectionId }
        ).then((res) => {
          if (res.code === 200) {
            resolve();
          }
        });
      });
      let deletePromise = new Promise((resolve, reject) => {
        if(deleteList.length === 0) {
          resolve();
          return
        }
        RemoveGroupFormSectionRequest(
          { id: this.id },
          {
            section_id: this.addSectionId,
            group_ids: deleteList,
          }
        ).then((res) => {
          if (res.code === 200) {
            resolve()
          }
        });
      });
      Promise.all([addPromise, deletePromise]).then(() => {
        this.showSection = false;
        this.$message({
          type: "success",
          message: "保存成功",
        });
        this.getSectionData();
      });
    },
    deleteHandle(item) {
      this.deleteClassifyHandle([item.section_id]);
    },
    deleteAllHandle() {
      if (this.groupSelects.length > 0) {
        this.deleteClassifyHandle(this.groupSelects);
      } else {
        this.$message({
          type: "warning",
          message: "请选择要删除的分组",
        });
      }
    },
    deleteClassifyHandle(data) {
      this.$confirm("此操作将永久删除该分组, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          DeleteSectionRequest({ id: this.id }, { section_ids: data }).then(
            (res) => {
              if (res.code === 200) {
                this.$message({
                  type: "success",
                  message: "删除成功",
                });
                this.getGroupData();
              }
            }
          );
        })
        .catch(() => {});
    },
    handleSelectionChange(rows) {
      this.selectGroupList = rows;
    },
    deleteAllGroupHandle() {
      if (this.selectGroupList.length > 0) {
        let list = this.selectGroupList.map((d) => d.group_id);
        this.deleteGroupHandle(list);
      } else {
        this.$message({
          type: "warning",
          message: "请选择要移除的群",
        });
      }
    },
    deleteGroupSingleHandle(item) {
      this.deleteGroupHandle([item.group_id]);
    },
    deleteGroupHandle(list) {
      this.$confirm("此操作将该群从分组中移除, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          RemoveGroupFormSectionRequest(
            { id: this.id },
            {
              section_id: this.groupForm.section_id,
              group_ids: list,
            }
          ).then((res) => {
            if (res.code === 200) {
              this.$message({
                type: "success",
                message: "移除成功",
              });
              this.getSectionData();
            }
          });
        })
        .catch(() => {});
    },
    searchSection() {
      if (this.groupForm.keyword) {
        this.groupLists = this.groupAllLists.filter((d) => {
          return d.section_name.includes(this.groupForm.keyword);
        });
      } else {
        this.groupLists = JSON.parse(JSON.stringify(this.groupAllLists));
      }
    },
    searchGroup() {
      if (this.groupForm.word) {
        this.markList = this.markAllLists.filter((d) => {
          return d.group_title.includes(this.groupForm.word);
        });
      } else {
        this.markList = JSON.parse(JSON.stringify(this.markAllLists));
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.page-box {
  width: 100vw;
  height: 100vh;
  background: #edf3fa;
  padding: 10px 0 10px;
  box-sizing: border-box;
}
.wapper-box {
  max-width: 1200px;
  margin: 0 auto;
  height: 100%;
  box-sizing: border-box;
  background: #fff;
  border-radius: 8px;
  display: flex;
}
.wapper-left {
  width: 320px;
  height: 100%;
  margin-right: 16px;
  padding: 16px 0;
  box-sizing: border-box;
}
.wapper-right {
  width: calc(100% - 346px);
  height: 100%;
  padding: 16px 0;
  box-sizing: border-box;
}
.left-header {
  display: flex;
  align-items: center;
  padding: 0 16px;
  margin-bottom: 10px;
}
.right-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;
  margin-bottom: 16px;
}
.input-right-search {
  width: 200px;
}
.check-item {
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;
}
.check-item:hover {
  background: #e7f1fe;
  cursor: pointer;
}
.check-item-active {
  background: #e7f1fe;
}
.left-btn-box {
  display: flex;
  justify-content: flex-end;
  padding: 0 16px;
  margin-bottom: 16px;
}
/deep/ .el-input__inner {
  height: 26px;
  line-height: 26px;
  font-size: 12px;
}
/deep/ .el-input-group__append {
  padding: 0 10px;
}
/deep/ .el-button {
  padding: 5px 10px;
  font-size: 12px;
}
/deep/ .el-checkbox {
  display: flex !important;
  align-items: center !important;
}
/deep/ .el-checkbox__label {
  padding-left: 5px;
  font-size: 14px !important;
}
/deep/ .el-checkbox__inner {
  width: 12px;
  height: 12px;
}
/deep/ .el-checkbox__input {
  display: flex;
  align-items: center;
}
.check-label {
  font-size: 12px;
  width: 220px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.icon-edit {
  color: #0d70f2;
  font-size: 14px;
  margin-right: 5px;
}
.icon-delete {
  color: #eb110a;
  font-size: 14px;
}
.right-content {
  height: calc(100% - 50px);
  padding: 0 16px;
}
/deep/ .el-table .el-checkbox {
  display: inline-block !important;
}
/deep/ .el-icon-search {
  cursor: pointer;
}
.emtry-box {
  color: #909399;
  text-align: center;
  font-size: 12px;
  margin-top: 30px;
}
.left-content {
  height: calc(100% - 75px);
  overflow: auto;
}
</style>
