<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    width="40%"
    :before-close="handleClose"
  >
    <el-transfer
      v-model="selectArray"
      :data="list"
      :props="transferProps"
      :titles="['待选群列表', '选中群列表']"
    ></el-transfer>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleClose">取 消</el-button>
      <el-button type="primary" @click="saveHandle">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { GetMyGroupRequest } from "@/api/app";
export default {
  props: {
    sectionId: {
      type: String,
      default: "",
    },
    id: {
      type: String,
      default: "",
    },
    selectList: {
      type: Array,
      default: () => [],
    },
  },
  watch: {
    sectionId() {
      this.getList();
    },
    selectList: {
      handler() {
        this.selectArray = JSON.parse(JSON.stringify(this.selectList));
      },
      deep: true,
      immediate: true,
    },
  },
  data() {
    return {
      dialogVisible: true,
      list: [],
      title: "添加群到分组",
      transferProps: {
        label: "group_title",
        key: "group_id",
      },
      selectArray: []
    };
  },
  mounted() {
    this.dialogVisible = true;
    this.getList();
  },
  methods: {
    handleClose(done) {
      this.$emit("close");
    },
    saveHandle() {
      this.$emit("save", this.selectArray);
    },
    getList() {
      if (!this.id) return;
      GetMyGroupRequest({ id: this.id }).then((res) => {
        if (res.code === 200) {
          this.list = res.data;
        }
      });
    },
  },
};
</script>