<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    width="30%"
    :before-close="handleClose"
  >
    <el-input placeholder="请输入分组名称" v-model="data.section_name"></el-input>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleClose">取 消</el-button>
      <el-button type="primary" @click="saveHandle">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
      required: true,
      default() {
        return {};
      }
    },
    type: {
      type: String,
      required: true,
      default: "add"
    }
  },
  data() {
    return {
      dialogVisible: true,
      title: "新增分组" 
    };
  },
  mounted() {
    this.dialogVisible = true;
    this.title = this.type === 'add' ? '新增分组' : '编辑分组'
  },
  methods: {
    handleClose(done) {
      this.$emit('close')
    },
    saveHandle() {
      if(this.data.section_name) {
        this.$emit('save', this.data)

      } else {
        this.$message({
          message: '分组名称不能为空',
          type: 'warning'
        });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
</style>